import React from "react";
import "./cheat_list.css";
import Cheat_Box_Preview from "components/cheats/cheat_box/cheat_box_preview";
import ClipLoader from "react-spinners/ClipLoader";
import { useCheats } from "hooks/useCheats";

const Cheat_List= ({cheatsFiltered,loadingCheatsFiltered,number}) => {
  const {loadingCheats, setLoadingCheats} = useCheats(false)
  if(loadingCheatsFiltered || loadingCheats){
    return <ClipLoader color={"red"} loading={true} css={'display: block;margin: 0 auto'} size={150} />
  }

  return (
      <div className="cheats_zone">
        <div className="cheat_list">
          {
            cheatsFiltered.length <1
              ?<span className="no_cheats">No cheats...</span>
              :
              number
              ?
              cheatsFiltered.slice(0,number).map((c,index) =>
                <Cheat_Box_Preview
                  key={index}
                  cheat={c}
                />
              )
              :cheatsFiltered.map((c,index) =>
                <Cheat_Box_Preview
                  key={index}
                  cheat={c}
                />
              )
          }
        </div>
      </div>
  );
};

export default Cheat_List;
